import SimplexNoise from 'simplex-noise';
import random from 'lodash/random';
const dpr = global.devicePixelRatio || 1;

const simplex = new SimplexNoise();

const getBackingStorePixelRatio = ctx =>
    ctx.webkitBackingStorePixelRatio ||
    ctx.mozBackingStorePixelRatio ||
    ctx.msBackingStorePixelRatio ||
    ctx.oBackingStorePixelRatio ||
    ctx.backingStorePixelRatio ||
    1;

const setupCanvas = canvas => {
    const width = (canvas.width = canvas.offsetWidth);
    const height = (canvas.height = canvas.offsetHeight);
    const ctx = canvas.getContext('2d');
    const bsr = getBackingStorePixelRatio(ctx);
    const ratio = dpr / bsr;
    if (ratio !== 1) {
        canvas.width = width * ratio;
        canvas.height = height * ratio;
        ctx.scale(ratio, ratio);
    }
    return {
        width,
        height,
    };
};

const createLine = (size, length, color, width, height) => {
    let x = random(-width * 2, width);
    let y = height + length + random(height * 2);
    let vx = 0;
    let vy = 0;

    let a = random(0.01, 0.5);
    let ax = a;
    let ay = -a;
    let dx = x + 1000;
    let dy = y - 1000;
    let spring = 0.005;

    const update = (width, height, mouseX, mouseY) => {
        vx = (dx - x) * spring;
        vy = (dy - y) * spring;

        vx += ax;
        vy += ay;

        x += vx;
        y += vy;
        if (x > width || y < 0) {
            x = random(-width, width);
            y = height + length + random(length);
            dx = x + 1000;
            dy = y - 1000;
        }
    };

    const draw = ctx => {
        ctx.beginPath();
        ctx.moveTo(x, y);
        ctx.lineTo(x + length, y - length);
        ctx.lineWidth = size;
        ctx.strokeStyle = color;
        ctx.stroke();
    };

    return {
        update,
        draw,
    };
};

const colors = ['#e4e4e4', '#777777', '#c1c1c1'];

export const createAnimation = canvas => {
    let raf;
    let mouseX = 0;
    let mouseY = 0;
    const { width, height } = setupCanvas(canvas);
    const ctx = canvas.getContext('2d');

    document.addEventListener('mousemove', event => {
        const { clientX, clientY } = event;
        mouseX = clientX;
        mouseY = clientY;
    });

    const lines = [];
    for (let i = 0; i < 200; i++) {
        lines.push(
            createLine(
                random(0.5, 1.5),
                random(20, 100),
                colors[random(0, colors.length - 1)],
                width,
                height
            )
        );
    }

    const update = () => {
        for (let i = 0, l = lines.length; i < l; i++) {
            lines[i].update(width, height, mouseX, mouseY);
        }
    };

    const draw = () => {
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        for (let i = 0, l = lines.length; i < l; i++) {
            lines[i].draw(ctx);
        }
    };

    const loop = () => {
        update();
        draw();
        raf = requestAnimationFrame(loop);
    };

    loop();
    return () => cancelAnimationFrame(raf);
};
